import React, { useState } from 'react';
import './App.css';

function App() {
  const answers = ['こたえ','回答','気候変動','46']
  const imagePaths = ['stage0.png','stage1.png','stage2.png','stage3.png']
  const [stage,setStage] = useState<number>(0)
  const [inputValue,setInputValue] = useState<string>('')
  const [imageSrc,setImageSrc] = useState<string>(imagePaths[0])
  const checkAnswer = () => {
    if (inputValue === answers[stage]) {
      setStage(stage+1)
      setImageSrc(imagePaths[stage+1])
      setInputValue('')
    }
  }
  return (
    <div className="App">
      <div>小泉謎</div>
      {
        stage < 4
         ? <>
            <div style={{marginTop: '32px'}}>
              <form>
                <input type="text" name="atext" value={inputValue}
                        onChange={(e) => setInputValue(e.target.value)}/>
                <button type="submit" onClick={(event)=> {event.preventDefault(); checkAnswer()}}>回答</button>
              </form>
            </div>
            <div style={{marginTop: '32px'}}>
              <img className="image-1" src={'./'+imageSrc} alt="question"/>
            </div>
         </>
         : <div style={{marginTop: '32px'}}>{"小泉謎をすべて解き明かした！"}</div>
      }


    </div>
  );
}

export default App;
